<template>
  <div class="song" :class="{hover: isCurrentMusic}">
    <div class="song_list_item">
      <player-music-item-check></player-music-item-check>
      <div v-if="!isMusicPlay" class="song_list_number">{{music.index}}</div>
      <img v-else-if="isCurrentMusic && isMusicPlay" class="song_list_gif" src="../../assets/img/wave.gif">
      <div class="song_list_item_name">{{music.name}}</div>
      <div class="song_list_item_menu">
        <a href="javascript:;" class="sprite_song_control" title=""
           :class="{list_menu_item_play: !isMusicPlay,
                    list_menu_item_pause: isMusicPlay}"
           @click="playOrPauseCurrentMusic()">
        </a
        ><a href="javascript:;" class="list_menu_item_add sprite_song_control" title="" @click="favorite"></a
        ><a href="javascript:;" class="list_menu_item_down sprite_song_control" title="" @click="favorite"></a
        ><a href="javascript:;" class="list_menu_item_share sprite_song_control" title="" @click="favorite"></a>
      </div>
      <div class="song_list_item_author"><a href="javascript:;">{{music.singer}}</a></div>
      <div class="song_list_item_time">{{music.time}}</div>
      <div><a href="javascript:;" class="song_list_item_delete sprite_song_control" title="" @click="favorite"></a></div>
    </div>
  </div>
</template>

<script>
  import PlayerMusicItemCheck from "./PlayerMusicItemCheck";
  import {messagePleaseWait} from "../../mixin";

  export default {
    name: "PlayerMusicItem",
    mixins: [messagePleaseWait],
    methods: {
      playOrPauseCurrentMusic() {
        if(this.currentMusicIndex !== this.storeMusicData.currentMusicIndex) {
          this.$store.commit('synchronizeMusicData',
              {prop: 'currentMusicIndex', val: this.currentMusicIndex});
          this.$store.commit('synchronizeMusicData',
              {prop: 'isMusicPlay', val: true});
        }
        else {
          this.$store.commit('synchronizeMusicData',
              {prop: 'isMusicPlay', val: !this.storeMusicData.isMusicPlay});
        }
        this.$store.commit('musicDataNeedUpdate', true);
      }
    },
    computed: {
      isMusicPlay() {
        return this.storeMusicData.isMusicPlay && this.storeMusicData.currentMusicIndex === this.currentMusicIndex;
      },
      isCurrentMusic() {
        return this.storeMusicData.currentMusicIndex === this.currentMusicIndex;
      },
      storeMusicData() {
        return this.$store.state.musicData;
      },
      isCurrentMusicAndPlay() {

      }
    },
    props: ['music', 'currentMusicIndex'],
    components: {
      PlayerMusicItemCheck
    }
  }
</script>

<style scoped>
  .song_list_item{
    height: 50px;
    line-height: 50px;
    display: flex;
    align-items: center;
    color: rgba(255, 255, 255, 0.6);
  }

  .song_list_number{
    display: inline-block;
    width: 32px;
    text-align: right;
  }

  .song.hover .song_list_number{
    color: rgba(255, 255, 255, 1);
  }

  .song_list_gif{
    width: 10px;
    height: 10px;
    margin-left: 22px;
  }

  .song_list_item_name{
    margin-left: 10px;
    width: 250px;
  }

  .song.hover .song_list_item_name{
    color: rgba(255, 255, 255, 1);
  }

  .song_list_item_menu{
    width: 200px;
    min-width: 200px;
  }

  .list_menu_item_play, .list_menu_item_pause, .list_menu_item_add, .list_menu_item_down, .list_menu_item_share{
    display: none;
    width: 36px;
    height: 36px;
    vertical-align: middle;
    margin-right: 10px;
  }

  .song_list_item_delete{
    display: none;
    width: 36px;
    height: 36px;
    vertical-align: middle;
    background-position: -80px -160px;
  }

  .song:hover .song_list_item_time,
  .song.hover .song_list_item_time{
    display: none;
  }

  .song_list_item_delete:hover{
    background-position: -120px -160px;
  }

  .list_menu_item_play{
    background-position: -80px 0;
  }

  .song:hover .list_menu_item_play,
  .song:hover .list_menu_item_pause,
  .song:hover .list_menu_item_add,
  .song:hover .list_menu_item_down,
  .song:hover .list_menu_item_share,
  .song:hover .song_list_item_delete,
  .song.hover .list_menu_item_play,
  .song.hover .list_menu_item_pause,
  .song.hover .list_menu_item_add,
  .song.hover .list_menu_item_down,
  .song.hover .list_menu_item_share,
  .song.hover .song_list_item_delete{
    display: inline-block;
  }

  .list_menu_item_play:hover{
    background-position: -120px 0;
  }

  .list_menu_item_pause{
    background-position: -80px -200px;
  }

  .list_menu_item_pause:hover{
    background-position: -120px -200px;
  }

  .list_menu_item_add{
    background-position: -80px -80px;
  }

  .list_menu_item_add:hover{
    background-position: -120px -80px;
  }

  .list_menu_item_down{
    background-position: -80px -120px;
  }

  .list_menu_item_down:hover{
    background-position: -120px -120px;
  }

  .list_menu_item_share{
    background-position: -80px -40px;
  }

  .list_menu_item_share:hover{
    background-position: -120px -40px;
  }

  .song_list_item_author{
    width: 200px;
  }

  .song{
    box-sizing: border-box;
    border-bottom: 1px solid rgba(255,255,255,.02);
  }

  .song_list_item_author > a{
    color: rgba(255, 255, 255, .6);
  }

  .song_list_item_author > a:hover{
    color: rgba(255, 255, 255, 1);
  }

  .song.hover .song_list_item_author > a{
    color: rgba(255, 255, 255, 1);
  }

  a{
    color: rgba(255, 255, 255, .8);
    text-decoration: none;
  }

  .sprite{
    background-image: url("../../assets/img/icon_sprite.png");
  }

  .sprite_song_control{
    background-image: url("../../assets/img/icon_list_menu.png");
  }
</style>