import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    musicData: {
      musicList: [],
      currentMusic: {},
      currentMusicIndex: 0,
      musicCurrentTime: '00:00',
      isMusicPlay: false,
      isMusicDataNeedUpdate: false,
      isPureStyle: false
    },
    checkState: {
      totalCheckNum: 0,
      checkedNum: 0,
      isAllCheck: false,
      checkType: 'normal'
    }
  },
  mutations: {
    synchronizeMusicData (state, payload) {
      state.musicData[payload.prop] = payload.val;
    },
    musicDataNeedUpdate(state, payload) {
      state.musicData.isMusicDataNeedUpdate = payload;
    },
    //
    checkAll(state, payload) {
      state.checkState.isAllCheck = payload.isAllCheck;
      state.checkState.checkType = payload.checkType
    },
    initTotalCheckNum(state, payload) {
      state.checkState.totalCheckNum = payload;
    },
    changeCheckedNum(state, payload) {
      state.checkState.checkedNum = payload;
    },
    addCheckedNum(state) {
      state.checkState.checkedNum += 1;
    },
    subCheckedNum(state) {
      state.checkState.checkedNum -= 1;
    }
  }
})