<template>
  <div class="footer_middle_progress"
    @touchstart="touchStart"
    ref="containBlock">
    <div class="footer_progress_load"
    :style="{width: loadRatio * 100 + '%'}"></div>
    <div class="footer_progress_play"
    :style="{width: progressRatioDisplay * 100 + '%'}">
      <i class="footer_progress_dot sprite_player"></i>
    </div>
    <div class="footer_progress_bg"></div>
  </div>
</template>

<script>
  export default {
    name: "ProgressLine",
    data() {
      return {
        progressLeftOffset: 0,
        progressWidth: 0,
        progressRatio: 0,
        isMouseDown: false
      }
    },
    methods: {
      touchStart(event) {
        const progressBounding = this.$refs.containBlock.getBoundingClientRect();
        this.progressLeftOffset = progressBounding.left;
        this.progressWidth = progressBounding.width;
        this.progressRatio = (event.touches[0].clientX - this.progressLeftOffset) / this.progressWidth;
        this.isMouseDown = true;
        window.addEventListener('touchmove', this.touchMove);
        window.addEventListener('touchend', this.touchEnd);
        if(this.isRealTime) {
          this.emitEvent();
        }
      },
      touchMove(event) {
        if(this.isMouseDown) { //mousemove
          this.progressRatio = (event.touches[0].clientX - this.progressLeftOffset) / this.progressWidth;
          if(this.progressRatio <= 0) {
            this.progressRatio = 0;
          }
          else if(this.progressRatio >= 1) {
            this.progressRatio = 1;
          }
          if(this.isRealTime) {
            this.emitEvent();
          }
        }
      },
      touchEnd() {
        if(this.isMouseDown) { //mouseup
          window.removeEventListener('touchmove', this.touchMove);
          this.isMouseDown = false;
          if (!this.isRealTime) {
            this.emitEvent();
          }
        }
      },
      emitEvent() {
        this.$emit('change-progress', this.progressRatio);
      }
    },
    computed: {
      progressRatioDisplay() {
        if(!this.isMouseDown) {
          return this.percentIn;
        }
        else {
          return this.progressRatio;
        }
      }
    },
    props: {
      percentIn: {
        type: Number,
        default: 0
      },
      loadRatio: {
        type: Number,
        default: 0
      },
      isRealTime: {
        type: Boolean,
        default: true
      }
    }
  }
</script>

<style scoped>
  .footer_middle_progress{
    width: 100%;
    box-sizing: border-box;
    cursor: pointer;
  }
  .footer_progress_load{
    width: 100%;
    height: 2px;
    background-color: rgba(255, 255, 255, .2);
    transition: width .1s linear;
  }

  .footer_progress_play{
    position: relative;
    top: -2px;
    width: 0;
    height: 2px;
    background-color: rgba(255, 255, 255, .7);
    /*transition: width .1s linear;*/
  }

  .footer_progress_dot{
    display: inline-block;
    position: absolute;
    top: -4px;
    right: -4px;
    width: 10px;
    height: 10px;
    background-position: 0 -170px;
  }

  .footer_progress_bg{
    position: relative;
    top: -4px;
    width: 100%;
    height: 2px;
    background-color: rgba(255, 255, 255, .1);
  }

  .sprite_player{
    background-image: url("../../assets/img/player.png");
  }
</style>