<template>
  <div class="footer">
    <div class="footer_left_control">
      <a href="javascript:;" class="btn_prev sprite_player" title="alt+" @click="prevMusic()"></a>
      <a href="javascript:;" class="btn_play sprite_player" title="[]"
         :class="{btn_play_pause: musicData.isMusicPlay}"
         @click="playOrPauseMusic()"></a>
      <a href="javascript:;" class="btn_next sprite_player" title="alt+" @click="nextMusic()"></a>
    </div>

    <div class="footer_middle">
      <div class="footer_middle_info">
        <a href="javascript:;" class="footer_music_name">{{musicName}}</a>
        -
        <a href="javascript:;" class="footer_singer_name">{{musicSinger}}</a>
      </div>
      <div class="footer_middle_time">{{musicData.musicCurrentTime}} / {{musicTotalTime}}</div>
      <progress-line v-bind="playProgressProp" @change-progress="updateProgressVal"></progress-line>
    </div>

    <div class="footer_right_control">
      <a href="javascript:;"
         class="btn_mode sprite_player"
         :style="playMode.style"
         @click="changePlayStyle"></a
      ><a href="javascript:;" class="btn_like sprite_player" @click="favorite"></a
      ><a href="javascript:;" class="btn_down sprite_player" @click="favorite"></a
      ><a href="javascript:;" class="btn_comment sprite_player" @click="favorite">
        <span class="comment_numbers">
            <i class="comment_num_9 sprite_comment"></i
            ><i class="comment_num_9 sprite_comment"></i
        ><i class="comment_num_9 sprite_comment"></i
        ><i class="comment_num_add sprite_comment"></i>
        </span>
      </a
      ><a href="javascript:;"
          class="btn_style sprite_player"
          :class="{btn_pure: musicData.isPureStyle}"
          @click="switchPureStyle"></a
      ><div class="player_voice">
        <a href="javascript:;"
           class="btn_voice sprite_player"
           :class="{btn_voice_mute: musicData.isMute}"
           @click="musicMute">
        </a>
        <div class="voice_progress">
          <progress-line v-bind="volumeProgressProp" @change-progress="updateVolume"></progress-line>
        </div>
      </div>
    </div>
    <audio src="" ref="audio" @ended="musicEnded"></audio>
  </div>
</template>

<script>
  import axios from "axios";
  import ProgressLine from "./ProgressLine";
  import {messagePleaseWait} from "../../mixin";

  export default {
    name: "PlayerFooter",
    data: function () {
      return {
        musicData: {
          musicCurrentTime: '00:00',
          musicCurrentProgress: 0,
          musicLoadRatio: 0,
          audio: {},
          audioVolume: 0,
          musicList: [],
          currentMusic: {},
          currentMusicIndex: 0,
          isMusicPlay: false,
          isMute: false,
          isPureStyle: false
        },
        playMode: {
          index: 0,
          style: {
            backgroundPosition: '0 -205px',
            height: '25px',
            marginTop: '0px'
          }
        },
        publicPath: process.env.BASE_URL
      }
    },
    watch: {
      isMusicDataNeedUpdate: function() {
        if(this.isMusicDataNeedUpdate) {
          this.updateDataFromStore();
          this.$store.commit('musicDataNeedUpdate', false);
        }
      }
    },
    mixins: [messagePleaseWait],
    methods: {
      updateDataFromStore() {
        const store = this.$store.state.musicData;
        //
        if(this.musicData.musicList !== store.musicList) {
          this.musicData.musicList = store.musicList;
        }
        //
        if(this.musicData.currentMusicIndex !== store.currentMusicIndex) {
          this.musicData.currentMusicIndex = store.currentMusicIndex;
          this.changeMusicByIndex();
        }
        ///
        if(this.musicData.isMusicPlay !== store.isMusicPlay) {
          this.musicData.isMusicPlay = store.isMusicPlay;
        }
        this.playMusic(this.musicData.isMusicPlay);
        // console.log('music data has synchronized from store');
      },
      updateDataToStore(syncDataName) {
        this.$store.commit("synchronizeMusicData",
            {prop: syncDataName, val: this.musicData[syncDataName]});
        // console.log(syncDataName + ' has synchronized to store');
      },
      changeMusicByIndex(newMusicIndex) {
        if(newMusicIndex) {
          this.musicData.currentMusicIndex = newMusicIndex;
        }
        this.musicData.currentMusic = this.musicData.musicList[this.musicData.currentMusicIndex];
        this.musicData.audio.src = this.musicData.currentMusic.link_url;
        this.musicData.musicCurrentTime = '00:00';
        this.musicData.musicCurrentProgress = 0;
        this.musicData.musicLoadRatio = 0;
        this.getCurrentMusicLyric();
        this.updateDataToStore('currentMusicIndex');
        this.updateDataToStore('currentMusic');
      },
      getCurrentMusicLyric() {
        const vm = this;
        this.musicData.currentMusic.lyric = [];
        this.musicData.currentMusic.times = [];
        axios({
          method:'get',
          url: this.musicData.currentMusic.link_lrc,
          responseType:'text'
        }).then(function(response) {
          let lyricArray;
          if(vm.publicPath === '/') {
            lyricArray = response.data.split("\r\n");
          }
          else {
            lyricArray = response.data.split("\n");
          }
          lyricArray.forEach(function (item, index) {
            //
            const lrc = item.split("]")[1];
            if(!lrc.length) return;
            vm.musicData.currentMusic.lyric.push(lrc);

            //
            const timeString = item.match(/\[(\d*:\d*\.\d*)\]/);
            if(timeString == null) return;
            const minAndSec = timeString[1].split(":");
            const min = parseInt(minAndSec[0]) * 60;
            const sec = parseFloat(minAndSec[1]);
            const time = (min + sec).toFixed(2);
            vm.musicData.currentMusic.times.push(time);
          });
        }, response => {
          console.log("error" + response);
        });
      },
      playMusic(newState) {
        if(newState) {
          this.musicData.audio.play();
          this.musicData.isMusicPlay = true;
        }
        else {
          this.musicData.audio.pause();
          this.musicData.isMusicPlay = false;
        }
        this.updateDataToStore('isMusicPlay');
      },
      playOrPauseMusic() {
        if(this.musicData.audio.src.length === 0) {
          this.changeMusicByIndex();
        }
        if(this.musicData.isMusicPlay) {
          this.playMusic(false);
        }
        else {
          this.playMusic(true);
        }
      },
      nextMusic() {
        this.musicData.currentMusicIndex += 1;
        if(this.musicData.currentMusicIndex > this.musicData.musicList.length - 1) {
          this.musicData.currentMusicIndex = 0;
        }
        this.changeMusicByIndex();
        this.playMusic(true);
      },
      prevMusic() {
        this.musicData.currentMusicIndex -= 1;
        if(this.musicData.currentMusicIndex < 0) {
          this.musicData.currentMusicIndex = this.musicData.musicList.length - 1;
        }
        this.changeMusicByIndex();
        this.playMusic(true);
      },
      computeCurrentTime(vm) {
        vm.$store.commit('synchronizeMusicData',
            {prop: 'musicCurrentTime', val: vm.musicData.audio.currentTime});
        let currentMin = (vm.musicData.audio.currentTime / 60).toFixed(0);
        let currentSec = (vm.musicData.audio.currentTime % 60).toFixed(0);
        if(currentMin < 10){
          currentMin = "0" + currentMin;
        }
        if(currentSec < 10){
          currentSec = "0" + currentSec;
        }
        vm.musicData.musicCurrentTime = currentMin + ":" + currentSec;
        vm.musicData.musicCurrentProgress = vm.musicData.audio.currentTime / vm.musicData.audio.duration;
        if(vm.musicData.audio.buffered.length > 0) {
          vm.musicData.musicLoadRatio = vm.musicData.audio.buffered.end(0) / vm.musicData.audio.duration;
        }
      },
      updateProgressVal(progressRatio) {
        this.musicData.audio.currentTime = this.musicData.audio.duration * progressRatio;
        this.computeCurrentTime(this);
      },
      updateVolume(progressRatio) {
        this.musicData.audioVolume = this.musicData.audio.volume = progressRatio;
      },
      musicEnded() {
        this.musicData.isMusicPlay = false;
        this.$store.commit('synchronizeMusicData', {prop: 'isMusicPlay', val: false});
      },
      musicMute() {
        this.musicData.isMute = !this.musicData.isMute;
        this.musicData.audio.muted = this.musicData.isMute;
      },
      changePlayStyle() {
        if(this.playMode.index < 3) {
          this.playMode.index ++;
        }
        else {
          this.playMode.index = 0;
        }

        switch (this.playMode.index) {
          case 1:
            this.playMode.style.backgroundPosition = '0 -257px';
            this.playMode.style.height = '25px';
            this.playMode.style.marginTop = '-6px';
            break;
          case 2:
            this.playMode.style.backgroundPosition = '0 -72px';
            this.playMode.style.height = '23px';
            this.playMode.style.marginTop = '0px';
            break;
          case 3:
            this.playMode.style.backgroundPosition = '0 -232px';
            this.playMode.style.height = '25px';
            this.playMode.style.marginTop = '0px';
            break;
          default:
            this.playMode.style.backgroundPosition = '0 -205px';
            this.playMode.style.height = '25px';
            this.playMode.style.marginTop = '0px';
              break;
        }
      },
      switchPureStyle() {
        this.musicData.isPureStyle = !this.musicData.isPureStyle;
        this.updateDataToStore('isPureStyle');
      }
    },
    computed: {
      isMusicDataNeedUpdate() {
        return this.$store.state.musicData.isMusicDataNeedUpdate;
      },
      musicName() {
        return this.musicData.currentMusic.name;
      },
      musicSinger() {
        return this.musicData.currentMusic.singer;
      },
      musicTotalTime() {
        return this.musicData.currentMusic.time;
      },
      playProgressProp() {
        return {
          percentIn: this.musicData.musicCurrentProgress,
          loadRatio: this.musicData.musicLoadRatio,
          isRealTime: false
        }
      },
      volumeProgressProp() {
        return {
          percentIn: this.musicData.audioVolume,
          loadRatio: 0,
          isRealTime: true
        }
      }
    },
    mounted() {
      this.musicData.audio = this.$refs.audio;
      this.musicData.audioVolume = this.musicData.audio.volume = 0.1;
      const vm = this;
      setInterval(function () {
        vm.computeCurrentTime(vm);
      },300);

      //
      axios({
        method:'get',
        url: this.publicPath + 'musiclist.json',
        responseType:'json'
      }).then(response => {
        response.data.forEach(function (item, index) {
          const musicItem = item;
          musicItem.link_url = musicItem.link_url.replace(/publicPath/, vm.publicPath);
          musicItem.cover = musicItem.cover.replace(/publicPath/, vm.publicPath);
          musicItem.link_lrc = musicItem.link_lrc.replace(/publicPath/, vm.publicPath);
          musicItem.index = index + 1;
          vm.musicData.musicList.push(musicItem);
        });
        vm.changeMusicByIndex();
        vm.updateDataToStore('musicList');
        vm.$store.commit('initTotalCheckNum', vm.musicData.musicList.length);
      }, response => {
        console.log("error" + response);
      });
    },
    components: {
      ProgressLine
    }
  }
</script>

<style scoped>
  .footer{
    display: flex;
    position: absolute;
    z-index: 3;
    bottom: 0;
    left: 0;
    right: 0;
    height: 67px;
    user-select: none;
    overflow: hidden;
  }

  .footer_left_control{
    width: 280px;
    height: 35px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
  }

  .footer_left_control > a{
    opacity: .8;
  }

  .footer_left_control > a:hover{
    opacity: 1;
  }

  .btn_prev{
    display: inline-block;
    width: 19px;
    height: 20px;
    margin-left: 77px;
    background-position: 0 -30px;
  }

  .btn_play{
    display: inline-block;
    width: 21px;
    height: 29px;
    margin-left: 46px;
    background-position: 0 0;
  }

  .btn_play_pause{
    background-position: -30px 0;
  }

  .btn_next{
    display: inline-block;
    width: 19px;
    height: 20px;
    margin-left: 46px;
    background-position: 0 -52px;
  }

  .footer_middle{
    width: 456px;
    min-width: 400px;
    height: 35px;
    margin-left: 20px;
    font-size: 14px;
    color: rgba(255, 255, 255, .8);
  }

  .footer_middle_info{
    display: inline-block;
    margin-bottom: 6px;
    height: 21px;
    line-height: 21px;
  }

  .footer_middle_info > a{
    color: rgba(255, 255, 255, .8);
  }

  .footer_middle_info > a:hover{
    color: rgba(255, 255, 255, 1);
  }

  .footer_middle_time{
    float: right;
    width: 90px;
    height: 21px;
    line-height: 21px;
  }

  .footer_right_control{
    width: 526px;
    height: 35px;
    line-height: 35px;
    white-space: nowrap;
  }

  .footer_right_control a{
    opacity: .8;
    vertical-align: middle;
  }

  .footer_right_control a:hover{
    opacity: 1;
  }

  .btn_mode{
    display: inline-block;
    width: 26px;
    height: 25px;
    vertical-align: middle;
    margin-left: 43px;
    background-position: 0 -205px;
    box-sizing: border-box;
  }

  .btn_like{
    display: inline-block;
    width: 23px;
    height: 21px;
    vertical-align: middle;
    margin-left: 24px;
    background-position: 0 -96px;
  }

  .btn_down{
    display: inline-block;
    width: 22px;
    height: 21px;
    vertical-align: middle;
    margin-left: 24px;
    background-position: 0 -120px;
  }

  .btn_comment{
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    vertical-align: middle;
    margin-left: 24px;
    background-position: 0 -430px;
  }

  .btn_comment:hover{
    background-position: 0 -490px;
  }

  .comment_numbers{
    position: absolute;
    top: -3px;
    right: -10px;
    line-height: 8px;
  }

  .comment_num_9{
    display: inline-block;
    width: 6px;
    height: 9px;
    background-position: -40px -210px;
  }

  .btn_comment:hover .comment_num_9{
    background-position: -40px -230px;
  }

  .comment_num_add{
    display: inline-block;
    width: 6px;
    height: 8px;
    background-position: -25px -125px;
  }

  .btn_comment:hover .comment_num_add{
    background-position: -25px -175px;
  }

  .btn_style{
    display: inline-block;
    width: 74px;
    height: 26px;
    vertical-align: middle;
    margin-left: 24px;
    background-position: 0 -282px;
  }

  .btn_pure{
    background-position: 0 -311px;
  }

  .player_voice{
    display: inline-block;
    vertical-align: middle;
    width: 112px;
    height: 26px;
    margin-left: 27px;
    cursor: pointer;
  }

  .btn_voice{
    display: inline-block;
    vertical-align: middle;
    margin-top: -12px;
    width: 26px;
    height: 21px;
    background-position: 0 -144px;
  }

  .btn_voice_mute{
    background-position: 0 -182px;
  }

  .voice_progress{
    display: inline-block;
    vertical-align: middle;
    margin-top: -12px;
    margin-left: 4px;
    width: 80px;
    height: 2px;
  }

  a{
    color: rgba(255, 255, 255, .8);
    text-decoration: none;
  }

  .sprite_song_control{
    background-image: url("../../assets/img/icon_list_menu.png");
  }

  .sprite_player{
    background-image: url("../../assets/img/player.png");
  }

  .sprite_comment{
    background-image: url("../../assets/img/comment.png");
  }
</style>