<template>
  <div class="song_list_check sprite"
       :class="{song_list_checked: isChecked}"
       @click="checkedOrNot()">
  </div>
</template>

<script>
  export default {
    name: "PlayerMusicItemCheck",
    data: function () {
      return {
        isChecked: false,
      }
    },
    methods: {
      checkedOrNot() {
        this.isChecked = !this.isChecked;
        if(this.checkType === 'checkAll') {
          this.$store.commit('checkAll', {isAllCheck: this.isChecked, checkType: this.checkType});
          if(this.isChecked) {
            this.$store.commit('changeCheckedNum', this.totalCheckNum);
          }
          else {
            this.$store.commit('changeCheckedNum', 0);
          }
        }
        else if(this.checkType === 'normal') {
          if(this.isChecked) {
            this.$store.commit('addCheckedNum');
          }
          else {
            this.$store.commit('checkAll', {isAllCheck: false, checkType: this.checkType});
            this.$store.commit('subCheckedNum');
          }
          if(this.checkedNum === this.totalCheckNum) {
            this.$store.commit('checkAll', {isAllCheck: true, checkType: 'checkAll'});
          }
        }
      }
    },
    props: {
      checkType: {
        type: String,
        default: 'normal'
      }
    },
    watch: {
      isAllCheck: function () {
        if(this.receivedCheckType === 'checkAll') {
          this.isChecked = this.isAllCheck;
        }
        else if(this.receivedCheckType === 'normal' && this.checkType === 'checkAll') {
          this.isChecked = false;
        }
      }
    },
    computed: {
      isAllCheck() {
        return this.$store.state.checkState.isAllCheck;
      },
      receivedCheckType() {
        return this.$store.state.checkState.checkType;
      },
      totalCheckNum() {
        return this.$store.state.checkState.totalCheckNum;
      },
      checkedNum() {
        return this.$store.state.checkState.checkedNum;
      }
    }
  }
</script>

<style scoped>
  .song_list_check{
    width: 16px;
    height: 16px;
    border: 1px solid rgba(225,225,225,.2);
    box-sizing: border-box;
    background-position: -1000px;
  }

  .song_list_check:hover{
    border: 1px solid rgba(225,225,225,1);
  }

  .song_list_checked{
    background-position: -60px -80px;
    border: 1px solid rgba(225,225,225,1);
  }

  .sprite{
    background-image: url("../../assets/img/icon_sprite.png");
  }
</style>