<template>
  <div class="song_info">
    <a href="javascript:;" class="song_info_cover">
      <img v-bind:src="musicPicPath" class="song_info_pic">
    </a>
    <div class="song_info_name">
      
      <a href="javascript:;">{{musicName}}</a>
    </div>
    <div class="song_info_singer">
      
      <a href="javascript:;">{{musicSinger}}</a>
    </div>
    <div class="song_info_album">
      
      <a href="javascript:;">{{musicAlbum}}</a>
    </div>
    <player-music-lyric></player-music-lyric>
  </div>
</template>

<script>
  import PlayerMusicLyric from "./PlayerMusicLyric";

  export default {
    name: "MusicInfo",
    computed: {
      musicPicPath() {
        return this.$store.state.musicData.currentMusic.cover;
      },
      musicName() {
        return this.$store.state.musicData.currentMusic.name;
      },
      musicSinger() {
        return this.$store.state.musicData.currentMusic.singer;
      },
      musicAlbum() {
        return this.$store.state.musicData.currentMusic.album;
      }
    },
    components: {
      PlayerMusicLyric
    }
  }
</script>

<style scoped>
  .song_info{
    position: absolute;
    top: 0;
    right: 0;
    width: 29%;
    height: 100%;
    color: rgba(255, 255, 255, .6);
    font-size: 14px;
    text-align: center;
  }

  .song_info a{
    color: rgba(255, 255, 255, .6);
  }

  .song_info a:hover{
    color: rgba(255, 255, 255, 1);
  }

  .song_info_cover{
    display: block;
    margin: 0 auto;
    width: 233px;
    height: 186px;
    background: url("../../assets/img/album_cover_player.png") 32px 0 no-repeat;
  }

  .song_info_pic{
    width: 186px;
    height: 186px;
  }

  .song_info_name, .song_info_singer, .song_info_album{
    height: 28px;
    line-height: 28px;
    text-overflow: ellipsis;
  }

  .song_info_name{
    margin-top: 15px;
  }

  a{
    color: rgba(255, 255, 255, .6);
    text-decoration: none;
  }
</style>