<template>
  <div class="mobile-app" :style="{height: mobileInnerHeight}">
    <m-player-header></m-player-header>
    <component v-bind:is="currentTabComponent"></component>
    <m-player-footer></m-player-footer>
    <div class="m-player-bg" :style="{backgroundImage: backgroundImgPath}"></div>
  </div>
</template>

<script>
  import MPlayerHeader from "./MPlayerHeader";
  import MPlayerMain from "./MPlayerMain";
  import MPlayerLyric from "./MPlayerLyric";
  import MPlayerFooter from "./MPlayerFooter";

  export default {
    name: "MobileApp",
    components: {
      MPlayerHeader,
      MPlayerMain,
      MPlayerFooter,
      MPlayerLyric
    },
    data() {
      return {
        mobileInnerHeight: window.innerHeight + 'px'
      }
    },
    computed: {
      backgroundImgPath() {
        return `url(${this.$store.state.musicData.currentMusic.cover})`;
      },
      isPureStyle() {
        return this.$store.state.musicData.isPureStyle;
      },
      currentTabComponent() {
        if(this.isPureStyle) {
          return MPlayerLyric;
        }
        else {
          return MPlayerMain;
        }
      }
    },
    created() {
      const vm = this;
      setInterval(function () {
        vm.mobileInnerHeight = window.innerHeight + 'px';
      },500)
    },
    beforeRouteEnter (to, from, next) {
      if (!(navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
        console.log('PC');
        next('/');
      }
      else {
        next();
      }
    },
  }
</script>

<style scoped>
  .mobile-app{
    width: 100vw;
    /*height: 100vh;*/
    position: relative;
    background-color: rgba(82, 82, 82, 0.5);
    transition: height .5s;
  }

  .m-player-bg{
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: center / cover;
    filter: blur(65px);
  }
</style>