<template>
  <div id="PCApp">
    <player-header></player-header>
    <player-main v-if="!isPureStyle"></player-main>
    <player-music-pure-lyric v-else></player-music-pure-lyric>
    <player-footer></player-footer>
    <div class="player_bg" v-bind:style="{backgroundImage: backgroundImgPath}"></div>
  </div>
</template>

<script>
  import PlayerHeader from "./PlayerHeader";
  import PlayerMain from "./PlayerMain";
  import PlayerMusicPureLyric from "./PlayerMusicPureLyric";
  import PlayerFooter from "./PlayerFooter";

  export default {
    name: 'PCApp',
    computed: {
      backgroundImgPath() {
        return 'url(' + this.$store.state.musicData.currentMusic.cover + ')';
      },
      isPureStyle() {
        return this.$store.state.musicData.isPureStyle;
      }
    },
    components: {
      PlayerHeader,
      PlayerMain,
      PlayerMusicPureLyric,
      PlayerFooter
    },
    beforeRouteEnter (to, from, next) {
      if ((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
        console.log('mobile');
        next('/mobile');
      }
      else {
        next();
      }
    },
  }
</script>

<style scoped>
  #PCApp{
    position: relative;
    height: 100vh;
    background-color: #525252;
    overflow: hidden;
  }

  .player_bg{
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: 50% / cover no-repeat;
    opacity: .6;
    filter: blur(65px);
  }
</style>