<template>
  <div class="song_list">
    <div class="song_list_item border_t_b">
      <player-music-item-check check-type="checkAll"></player-music-item-check>
      <div class="song_list_header_name"></div>
      <div class="song_list_header_author"></div>
      <div class="song_list_header_time"></div>
    </div>

    <div class="song_list_content">
      <el-scrollbar style="height:100%" ref="scrollBar">
        <player-music-item
            v-for="(music, index) in musicList"
            v-bind="{music: music, currentMusicIndex: index}"
        ></player-music-item>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
  import PlayerMusicItem from "./PlayerMusicItem";
  import PlayerMusicItemCheck from "./PlayerMusicItemCheck";

  export default {
    name: "MusicList",
    methods: {
      autoScroll() {
        const currentMusicIndex = this.currentMusicIndex;
        if(currentMusicIndex > 5) {
          const scrollNeedTo = (currentMusicIndex - 5) * 50;
          const currentScroll = this.$refs.scrollBar.wrap.scrollTop;
          if(currentScroll < scrollNeedTo) {
            this.$refs.scrollBar.wrap.scrollBy({
              top: scrollNeedTo,
              left: 0,
              behavior: 'smooth'
            });
          }
        }
      }
    },
    watch: {
      currentMusicIndex: function () {
        this.autoScroll();
      }
    },
    computed: {
      musicList() {
        return this.$store.state.musicData.musicList;
      },
      currentMusicIndex() {
        return this.$store.state.musicData.currentMusicIndex;
      }
    },
    components: {
      PlayerMusicItem,
      PlayerMusicItemCheck
    }
  }
</script>

<style scoped>
  .song_list{
    width: 71%;
    height: 361px;
    color: rgba(255, 255, 255, .8);
    font-size: 14px;
    margin-top: 16px;
  }

  .song_list_item{
    height: 50px;
    line-height: 50px;
    display: flex;
    align-items: center;
  }

  .border_t_b{
    border-top: 1px solid rgba(255,255,255,.02);
    border-bottom: 1px solid rgba(255,255,255,.02);
  }

  .song_list_header_name{
    margin-left: 43px;
    width: 450px;
  }

  .song_list_header_author{
    width: 130px;
  }

  .song_list_header_time{
    width: 102px;
    text-align: right;
  }

  .song_list_content{
    height: 300px;
    overflow: auto;
  }
</style>

<style>
  div.el-scrollbar__wrap {
    overflow-x: hidden;
  }
</style>