<template>
  <div class="player-header">
    <div class="music-list-btn" @click="unfoldMusicList">
      <i class="el-icon-s-unfold"></i>
    </div>
    <div class="header-middle">
      <a href="#" :class="{select: selectState[0]}" @click="switchContent(0)"></a>
      <i class="divide"></i>
      <a href="#" :class="{select: selectState[1]}" @click="switchContent(1)"></a>
      <i class="divide"></i>
      <a href="#" :class="{select: selectState[2]}" @click="switchContent(2)"></a>
    </div>
    <div class="share">
      <i class="el-icon-share"></i>
    </div>

    <el-drawer
      custom-class="music-list"
      :visible.sync="drawer.visible"
      direction="ltr"
      size="80%"
      @opened="drawerOpen">
      <template v-slot:title>
        <span class="list-title"></span>
      </template>

      <div class="musics">
        <div class="music-item"
             v-for="(item, index) in storeMusicList"
             @click="playThisMusic(index)"
             ref="musics">
          <div class="music-index"><span>{{index + 1}}</span></div>
          <div class="music-info">
            <div class="music-name"> {{item.name}}</div>
            <div class="music-singer">{{item.singer}}</div>
          </div>
        </div>
      </div>
    </el-drawer>

  </div>
</template>

<script>
  export default {
    name: "MPlayerHeader",
    data() {
      return {
        musicList: null,
        selectState: [false, true, false],
        drawer: {
          visible: false
        }
      }
    },
    watch: {
      currentMusicIndex: function () {
        if(this.musicList) {
          this.musicList.forEach(function (item) {
            item.classList.remove('playing');
          })
          this.musicList[this.currentMusicIndex].classList.add('playing');
        }
      }
    },
    methods: {
      switchContent(index) {
        for(let i = 0; i < this.selectState.length; i++) {
          this.$set(this.selectState, i, false);
        }
        this.$set(this.selectState, index, true);
        const store = this.$store;
        switch (index) {
          case 0:
            break;
          case 1:
            store.commit('synchronizeMusicData', {prop: 'isPureStyle', val: false});
            break;
          case 2:
            store.commit('synchronizeMusicData', {prop: 'isPureStyle', val: true});
            break;
          default:
            break;
        }
      },
      unfoldMusicList() {
        this.drawer.visible = !this.drawer.visible;
      },
      playThisMusic(index) {
        this.$store.commit('synchronizeMusicData', {prop: 'currentMusicIndex', val: index});
        this.$store.commit('synchronizeMusicData', {prop: 'isMusicDataNeedUpdate', val: true});
        this.$store.commit('synchronizeMusicData', {prop: 'isMusicPlay', val: true});
      },
      drawerOpen() {
        this.musicList = this.$refs.musics;
        this.musicList.forEach(function (item) {
          item.classList.remove('playing');
        })
        this.musicList[this.currentMusicIndex].classList.add('playing');
      }
    },
    computed: {
      storeMusicList() {
        return this.$store.state.musicData.musicList;
      },
      musicName() {
        return this.$store.state.musicData.currentMusic.name;
      },
      musicSinger() {
        return this.$store.state.musicData.currentMusic.singer;
      },
      currentMusicIndex() {
        return this.$store.state.musicData.currentMusicIndex;
      }
    }
  }
</script>

<style lang="less" scoped>
  @normal-color: rgba(255, 255, 255, .6);
  @select-color: rgba(255, 255, 255, 1);
  .test-border{
    box-sizing: border-box;
    border: 1px red solid;
  }

  .player-header{
    width: 90%;
    height: 8%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;

    .music-list-btn{
      flex: 1;
      height: 100%;
      font-size: 23px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .header-middle{
      flex: 9;
      height: 100%;
      font-size: 13px;
      display: flex;
      justify-content: center;
      align-items: center;

      .divide{
        display: inline-block;
        width: 1px;
        height: 10px;
        margin-left: 12px;
        margin-right: 12px;
        background-color: @normal-color;
      }

      .select{
        color: @select-color;
      }
    }

    .share{
      flex: 1;
      height: 100%;
      font-size: 23px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    i{
      color: @normal-color;
    }

    a{
      text-decoration: none;
      color: @normal-color;
    }
  }
</style>

<style lang="less">
  @normal-color: rgba(255, 255, 255, .6);

  .player-header .music-list{
    background-color: rgba(82, 82, 82, 1);
    color: @normal-color;

    .el-drawer__header{
      height: 8%;
      margin: 0;
      padding: 0 20px 0 20px;

      .list-title{
        text-align: center;
        color: @normal-color;
      }
    }

    .el-drawer__body{
      height: 92%;

      .musics{
        height: 100%;
        overflow: auto;
        border-top: 1px solid @normal-color;
        font-size: 16px;

        .music-item{
          display: flex;
          margin-top: 10px;
          padding-bottom: 5px;
          border-bottom: 1px solid rgba(255, 255, 255, .1);

          .music-index{
            flex: 1;
            text-align: center;

            span{
              line-height: 33px;
            }
          }

          .music-info{
            flex: 5;

            .music-singer{
              font-size: 8px;
              margin-top: 6px;
            }
          }
        }

        .playing{
          color: #31c27c;
        }
      }
    }
  }
</style>