<template>
  <div class="song_info_lyric">
    <div class="song_info_lyric_inner"
         :style="styleObj">
      <p ref="lyric" v-for="(item) in storeLyrics">{{item}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PlayerMusicLyric",
    data: function() {
      return {
        currentLyricIndex: 0,
        transitionDuration: 0.5,
      }
    },
    watch: {
      musicCurrentTime: function () {
        this.translateLyric(this.musicCurrentTime);
        if(this.musicCurrentTime < 0.3) {
          this.transitionDuration = 0;
        }
        else {
          this.transitionDuration = 0.5;
        }
      }
    },
    methods: {
      translateLyric(currentTime) {
        const vm = this;
        this.currentLyricIndex = this.storeLyricTimes.findIndex(function (item, index) {
          if (index < vm.storeLyricTimes.length - 1
              && currentTime >= vm.storeLyricTimes[index]
              && currentTime < vm.storeLyricTimes[index + 1]) {
            return true;
          }
          else if(index === vm.storeLyricTimes.length - 1
              && currentTime >= vm.storeLyricTimes[index - 1]) {
            return true;
          }
          else {
            return false;
          }
        });
        if(this.currentLyricIndex < 0) {
          this.currentLyricIndex = 0;
        }
        const lyricList = this.$refs.lyric;
        if(lyricList && lyricList.length !== 0) {
          lyricList.forEach(function (item) {
            item.classList.remove('on');
          })
          lyricList[this.currentLyricIndex].classList.add('on');
        }
      }
    },
    computed: {
      storeLyrics() {
        const lyrics = this.$store.state.musicData.currentMusic.lyric;
        if(lyrics) {
          return lyrics;
        }
      },
      musicCurrentTime() {
        return this.$store.state.musicData.musicCurrentTime;
      },
      storeLyricTimes() {
        const times = this.$store.state.musicData.currentMusic.times
        if(times) {
          return times;
        }
      },
      styleObj() {
        return {
          transform: `translateY(${this.currentLyricIndex * -30}px)`,
          transition: `transform ${this.transitionDuration}s`
        }
      }
    }
  }
</script>

<style scoped>
  .song_info_lyric{
    width: 300px;
    height: 70px;
    margin: 15px auto 0 auto;
    overflow: hidden;
    mask-image: linear-gradient(to bottom,rgba(255,255,255,0) 0,rgba(255,255,255,.6) 15%,
    rgba(255,255,255,1) 25%,rgba(255,255,255,1) 75%,
    rgba(255,255,255,.6) 85%,rgba(255,255,255,0) 100%);
  }

  .song_info_lyric_inner{
    margin-top: 20px;
    line-height: 30px;
  }

  .song_info_lyric_inner > p{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .song_info_lyric_inner > p.on{
    color: #31c27c;
  }
</style>